import React, {useState} from 'react';
import Button from '../Button/Button';

// EmailJs
import emailjs from 'emailjs-com';
// React Hook Form for Vaidation
import { useForm } from "react-hook-form";


import './contact.scss'


const Contact = ({buttonStyles}) => {

 // the hook for the message in the email.js sendForm promise
const [successMessage, setSuccessMessage] = useState('');

// REACT FORM HOOK IMPORT
const { register, handleSubmit, errors} = useForm();

// THE INFO FROM WE NEED FROM THE EMAIL.JS FORM TEMPLATE
const serviceID = 'service_098mufm';
const templateID = 'template_098mufm';
const userID = 'user_wgjQiuBIoX0lwtbRCpmiQ'; // get this from user => integ => API

// THIS CODE IS FROM THE REACT HOOK FORM SITE
const onSubmit = (data, r) => {
    sendEmail(
      serviceID,
      templateID,
      {
        name: data.name,
        phone: data.phone,
        email: data.email,
        subject: data.subject,
        description: data.description
      },
      userID
    )
    r.target.reset();
  }

   
// THIS IS WHAT WE NEED FROM THE EMAIL.JS SITE
  const sendEmail = (serviceID, templateID, variables, userID) => {
    emailjs.send(serviceID, templateID, variables, userID)
      .then(() => {
        setSuccessMessage("Form sent successfully! I'll contact you as soon as possible.");
      }).catch(err => console.error(`Something went wrong ${err}`));
  }


  // INSIDE THE FORM WE NEED TO APPLY VALIDATION USING 'REF' - SEE REACT HOOK WEBSITE

    return (
       <React.Fragment>

            <div className="contacts">

                <div className="text-center">
                <h1>Contact me</h1>
                <p>Please fill out this form and describe your contact needs. I will contact you as soon as possible</p>
                <span className="success-message">
                    {successMessage}
                </span>
                </div>
               

                <div className="container">

                {/* add the handleSubmit function using the onsubmit as its param */}
           <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="row">
                        <div className="col-md-6 col-xs-12">
                        
                        {/* NAME INPUT */}
                        <div className="text-center">
                            <input
                            type="text"
                            className="form-control"
                            placeholder="Name"
                            name="name"
                            ref={
                                register({
                                required: "Please enter your name",
                                maxLength: {
                                    value: 20,
                                    message: "Please enter a name with fewer than 20 characters"
                                }
                                })
                            }
                            />
                            <div className="line"></div>
                        </div>
                        <span className="error-message">
                            {errors.name && errors.name.message}
                        </span>
                        {/* PHONE INPUT */}
                        <div className="text-center">
                            <input
                            type="text"
                            className="form-control"
                            placeholder="Phone Number"
                            name="phone"
                            ref={
                                register({
                                required: "Please add your phone number",
                                })
                            }
                            />
                            <div className="line"></div>
                        </div>
                        <span className="error-message">
                            {errors.phone && errors.phone.message}
                        </span>
                        {/* EMAIL INPUT */}
                        <div className="text-center">
                            <input
                            type="email"
                            className="form-control"
                            placeholder="Email"
                            name="email"
                            ref={
                                register({
                                required: "Please provide you email",
                                pattern: {
                                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                    message: "invalid Email"
                                }
                                })
                            }
                            />
                            <div className="line"></div>
                        </div>
                        <span className="error-message">
                            {errors.email && errors.email.message}
                        </span>
                        {/* SUBJECT INPUT */}
                        <div className="text-center">
                            <input
                            type="text"
                            className="form-control"
                            placeholder="Subject"
                            name="subject"
                            ref={
                                register({
                                required: "OOPS, you forget to add the subject.",
                                })
                            }
                            />
                            <div className="line"></div>
                        </div>
                        <span className="error-message">
                            {errors.subject && errors.subject.message}
                        </span>
                        </div>
                        <div className="col-md-6 col-xs-12">
                        {/* DESCRIPTION */}
                        <div className="text-center">
                            <textarea
                            type="text"
                            className="form-control"
                            placeholder="Please describe shortly you project..."
                            name="description"
                            ref={
                                register({
                                required: "Please describe shortly your project needs...",
                                })
                            }
                            ></textarea>
                            <div className="line"></div>
                        </div>
                        <span className="error-message">
                            {errors.description && errors.description.message}
                        </span>
                        <Button buttonStyles="btn--outline">Contact Me</Button>
                        </div>
                    </div>
                    </form>
          
          
          </div>
          {/* close container */}



            </div>
            {/* close contacts wrapper */}

       </React.Fragment>
    )
}

export default Contact;
